import Vue from 'vue'
import Router from 'vue-router'
import Home from '../page/home/Home'
import Audio from '../page/audio/Audio'
import Group from '../page/group/Group'
import Mine from '../page/mine/Mine'
import Broadcast from '../page/broadcast/Broadcast'
import Film from '../components/Film'
import Book from '../components/Book'
import Tv from '../components/Tv'
import City from '../components/City'
import Music from '../components/Music'

// 导入模板

// 使用vue-router路由文件
Vue.use(Router)

// 设置实例路由
export default new Router({
  routes: [
    {
      path: '/',		  // 分配路径
      name: '/',		  // 路径名称
      component:Home	  // 分配路径所需组件
    },
    {
    	path:'/home',
    	name:'Home',
    	component: Home
    },
    {
    	path:'/audio',
    	name:'Audio',
    	component: Audio
    },
    {
    	path:'/group',
    	name: 'Group',
    	component: Group
    },
    {
    	path:'/broadcast',
    	name: 'Broadcast',
    	component: Broadcast,
        children:[      // 二级路由配置，path 不带斜线，roter-link处的 ‘to’ 属性值需跟上完整路径 ‘/1级路径名/2级路径名’
            {
                path:'/',   // path 只跟 ‘/’ 为配置路由默认显示页面
                component:Film
            },
            {
                path:'film',
                name:'Film',
                component:Film
            },
            {
                path:'book',
                name:'Book',
                component:Book
            },
            {
                path:'tv',
                name:'Tv',
                component:Tv
            },
            {
                path:'city',
                name:'City',
                component:City
            },
            {
                path:'music',
                name:'Music',
                component:Music
            }
        ]
    },
    {
    	path:'/mine',
    	name:'Mine',
    	component:Mine
    }

  ]
})



