<template>
	<div class="grop_top_join">
		<div>
			<p><span>{{ joinedNum }}</span>个小组等你加入</p>
			<p>立即选择加入吧!</p>
		</div>
		
		<img src="../assets/images/ic_group_banner.png">
	</div>
</template>

<script>
	export default{
		props:['joinedNum']
	}
</script>

<style>
	.grop_top_join{text-align: center;height: 140px;position: relative;}
	.grop_top_join>div{position: absolute;z-index: 1;padding-top:40px;width: 100%;}
	.grop_top_join p:first-child{color: #777;font-size: 16px;font-weight: 300}
	.grop_top_join p:last-child{color: #c1c1c1;font-size: 14px;}
	.grop_top_join img{position: absolute;width:100%;height: 130px;left: 0;top: 0;}
</style>


