<template>
	<div class="mine_info">
		<img src="../assets/images/ic_my_default_bg.jpg" />
		<div class="mine_info_state">
			<div class="mine_icon">
				<img src="../assets/images/avatar_male_70.png">
			</div>
			<div class="login_state">{{ loginState }}</div>
		</div>
	</div>
</template>

<script>
	export default{
		data:function(){
			return {
				loginState:'登录/注册'
			}
		}
	}
</script>
<style>
	.mine_info{height: 135px;width: 100%;background-color: #fff;position: relative;}
	.mine_info>img{position: absolute;width: 100%;height: 100%;}
	.mine_info_state{position: absolute;z-index: 1;width: 100%;height: 100%;background-color: transparent;}
	.mine_info_state>div{float: left;}
	.mine_info_state img{width: 80px;padding: 25px 50px 0 20px;}
	.login_state{margin-top:50px;text-align: center;width: 150px;height: 40px;border: 2px solid #efefef;border-radius: 5px;line-height: 40px;cursor: pointer;}
</style>


