<template>
	<div class="mine_remind">
		<div class="mine_remind_top">
			<div class="mine_remind_icon">
				<img src="../assets/images/ic_mine_notification.png">
			</div>
			<span>提醒</span>
			<div class="more_icon">
				<img src="../assets/images/ic_seti_arrow_right_gray.png" alt="">
			</div>
		</div>
		<div class="mine_remind_cont">
			<ul v-if="remindArr.length>0">
				<li v-for="item in remindArr">{{ item }}</li>
			</ul>
			<div class="mine_remind_txt">
				<p>暂无新提醒</p>
			</div>
		</div>
	</div>
</template>

<script>
	
	export default{
		data(){
			return {
				remindArr:[]
			}
		}
	}
</script>

<style>
	.mine_remind_top{border-bottom: 1px solid #f3f3f3;padding:15px 0 5px;height: 30px;}
	.mine_remind_top span{float:left;line-height: 30px;padding-left: 20px;font-size: 14px;font-weight: bold;}
	.mine_remind{height:105px;background-color: #fff;margin: 8px 0;padding-left: 20px;}
	.mine_remind_icon{width: 30px;height:30px;float:left;}
	.mine_remind_top img{width: 100%;height: 100%;}
	.more_icon{float: right;width: 8px;padding:10px 20px 0 0;}
	.mine_remind_txt{text-align: center;line-height: 50px;height: 50px;font-size: 14px;color: #ccc;}
</style>


