<template>
	<div class="mine_app">
		<slot name="appIcon"></slot>
		<p>{{ txt }}</p>
	</div>
</template>

<script>
	export default{
		props:['txt']
	}
</script>

<style>
	.mine_app{width: 25%;text-align: center;border-bottom: 1px solid #f3f3f3;float: left;padding-bottom: 20px;}
	.mine_app img{width: 30px;padding: 20px 0 5px;}

</style>


