<template>
	<div class="group_reco_column">
		<h4>{{ columnName }}</h4>
		<div class="column_img">
			<slot name="columnImg"></slot>
		</div>
		
	</div>
</template>

<script>
	export default{
		props:['columnName']
	}
</script>

<style>
	.group_reco_column{position: relative;}
	.group_reco_column h4{position: absolute;left: 15px;bottom: 15px;color: #f2f2f2;font-size: 16px;font-weight: normal;}
	.column_img,.column_img img{width: 100%;}
</style>


