<template>
	<div class="tabbar">
		<wrap :sel="selected">
			<tabbar-item v-for="item in imgArr" :txt="item.txt" :id="item.id" @sendval="getVal">
				<img :src="item.normal" slot="normalImg">
				<img :src="item.active" slot="activeImg">
			</tabbar-item>
		</wrap>
	</div>
</template>

<script>
	import Wrap from './Wrap'
	import TabbarItem from './TabbarItem'

	export default {
		components:{
			Wrap,
			TabbarItem
		},
		data:function(){
			return {
				imgArr:[
					{	
						normal:require("../assets/images/ic_tab_home_normal.png"),
						active:require("../assets/images/ic_tab_home_active.png"),
						txt:'首页',
						id:'home'
					},
					{	
						normal:require("../assets/images/ic_tab_subject_normal.png"),
						active:require("../assets/images/ic_tab_subject_active.png"),
						txt:'书影音',
						id:'broadcast'
					},
					{	
						normal:require("../assets/images/ic_tab_status_normal.png"),
						active:require("../assets/images/ic_tab_status_active.png"),
						txt:'广播',
						id:'audio'
					},
					{	
						normal:require("../assets/images/ic_tab_group_normal.png"),
						active:require("../assets/images/ic_tab_group_active.png"),
						txt:'小组',
						id:'group'
					},
					{	
						normal:require("../assets/images/ic_tab_profile_normal.png"),
						active:require("../assets/images/ic_tab_profile_active.png"),
						txt:'我的',
						id:'mine'
					},
				],
				selected:'home'
			}
		},
		methods:{
			getVal:function(val){
				this.selected = val;
			}
		}
	}

</script>

<style>
	.tabbar{position: fixed;bottom: 0;width: 100%;height: 65px;border-top: 1px solid #dfdfdf;text-align: center;background-color: #f7f7f7;z-index: 10}
	.tabbar img{width: 40px;}
</style>


