<template>
	<div class="header" :style="{backgroundColor:bcol,color:txtcol}">
		<slot></slot>
		<slot name="leftImg"></slot>
		<slot name="centerImg"></slot>
		<slot name="rightImg"></slot>
	</div>
</template>

<script>
	export default{
		props:{
			bcol:{
				default:'#efefef',
			},
			txtcol:{
				default:'#666'
			}
		}
	}
</script>

<style>
	.header{height:30px;color: #494949;letter-spacing: 2px;font-size: 16px;padding: 15px;text-align: center;background-color: #f7f7f7;border-bottom: 1px solid #dfdfdf;position: fixed;top: 0;left:0;right:0;z-index: 10;}
	.header img{position: absolute;}
	.header input{display: block;}
	.header p{line-height: 30px;}
	.h_left_img{bottom: 20px;left: 25px;width: 21px;}
	.h_center_img{bottom: 20px;right: 16%;width: 21px;}
	.h_right_img{right: 3%;width: 25px;bottom: 18px;}
	.header input::placeholder{color: #ccc;}
</style>


