<template>
	<div class="broadcast_item">
    <div class="broadcast_item_cont" @click="showItem">
      <div class="item_img">
        <slot name="itemImg"></slot>
      </div>
      <div>
        <p class="item_name">{{ itemName }}</p>
        <slot name="item_art"></slot>
        <div class="item_evaluation" v-if="itemScore>0">
          <div class="item_star">
            <slot name="star1"></slot>
            <slot name="star2"></slot>
            <slot name="star3"></slot>
            <slot name="star4"></slot>
            <slot name="star5"></slot>
          </div>
          <div class="item_score">
            <span>{{ itemScore }}</span>
          </div>
        </div>
        <div v-else-if="!itemNoScore" class="has_no_score"><span>暂无评分</span></div>
        <div class="has_want_num" v-else> <span>{{ itemWantNum }}人想看</span></div>
      </div>
    </div>
    <transition name="maskLayer">
      <div class="mask" v-if="itemShow">
        <div class="go_back" @click="showItem">
          <span><</span>
        </div>
        <div class="item_content">
            <h2> {{ itemContName }} </h2>
            <p>
              电影演员介绍电影演员介绍电影演员介绍电影演员介绍  
            </p>
            <div class="btn_list">
              <button type="button">想看</button><button type="button">看过</button>
            </div>
            <p><strong>{{ itemContName }}</strong> 剧情介绍:
             亚马逊公主戴安娜·普林斯（盖尔·加朵 Gal Gadot 饰），经过在家乡天堂岛的训练，取得上帝赐予的武器与装备，化身神奇女侠，与空军上尉史蒂夫·特雷弗（克里斯·派恩 Chris Pine 饰）一同来到人类世界，一起捍卫和平、拯救世界，在一战期间上演了震撼人心的史诗传奇。(根本就是神奇女侠剧情~~逃ing~)  
            </p>
            <p class="title">查看更多豆瓣高分电影电视剧</p>
            <div class="tag_list">
              <ul>
                <li>DC</li>
                <li>超级英雄</li>
                <li>女神</li>
                <li>美国</li>
                <li>2017</li>
                <li>漫画改编</li>
                <li>动作</li>
                <li>科幻</li>
              </ul>
            </div>
            <p class="title video_side">查看神奇女侠的预告片</p>
            <div class="video_cont">
              <video  controls muted>
                <source src="../../static/film/film.mp4" type="video/mp4">
                您的浏览器不支持视频播放
              </video>
            </div>
            <div class="item_comment">
              <p class="title">{{ itemContName }} 的短评</p>
              <ul class="comment_list">
                <li v-for="item in comment" class="clear">
                  <div class="img_cont">
                    L
                  </div> 
                  <div>
                    <strong>{{ item.userNmae }}</strong><br>
                    <time>{{ item.data }}</time>
                    <p>{{ item.comment }}</p>
                  </div>
                </li>    
              </ul>
              <p class="title duanpin">查看全部短评</p>
            </div>
        </div>
      </div>
    </transition> 
	</div>
</template>

<script>
export default{
	props:['itemName','itemScore','itemWantNum','itemNoScore'],
  data () {
    return {
      itemShow: false,
      itemContName:'',
      comment:[
        {
          "data": "2017-05-25 1:16:55",
          "userNmae": "MIS奥卡",
          "comment": "修仙福利!画咱收了!"
        },
        {
          "data": "2017-05-26 10:16:55",
          "userNmae": "aamikoduaa",
          "comment": "快把我从折叠区里放出来！！"
        },
        {
          "data": "2017-05-27 10:16:55",
          "userNmae": "MIS奥卡",
          "comment": "很赞啊！！"
        },
        {
          "data": "2017-05-28 1:16:55",
          "userNmae": "啊阿莫sdkn",
          "comment": "画不错!意境已经超脱凡胎!"
        },
        {
          "data": "2017-05-28 11:10:55",
          "userNmae": "暗杀宝贝儿~",
          "comment": "画不错!意境已经超脱凡胎!"
        }
      ]
    }
  },
  methods: {
    showItem () {
      this.itemShow = !this.itemShow;
      this.itemContName = this.itemName
     },
 }
}
</script>
<style>
	.broadcast_item{flex: 1;}
	.broadcast_item .item_name{font-size: 14px;padding: 4px 0;color: #333;overflow: hidden;height: 19px;line-height: 19px;}
	.item_img{width: 90px;height: 125px;}
	.broadcast_item img{width: 100%;height: 100%;float: left;}
	.item_evaluation{height: 16px;}
	.item_evaluation>div{height: 16px;float: left;}
	.item_star img{float: left;width: 12px;}
	.item_score{font-size: 12px;padding-left: 10px;float: left;line-height: 16px;color:#e09015;}
	.has_no_score{color:#9c9c9c;font-size: 12px;}
	.has_want_num{font-size: 12px;color: #888;}
	.broadcast_item .item_art{font-size: 12px;padding:0 0 5px 0;color: #a6a6a6;}
  .broadcast_item_cont{margin: 0 auto;width: 90px;}
  .mask{position: fixed;height: 100vh;width: 100vw;background-color: #fff;left: 0;top: 0;z-index: 101;overflow: auto;}

  .go_back{height: 40px;font-family: 'consolas';line-height: 40px;text-align: center;background-color: rgba(140,140,140,.1);}
  .go_back span{display: block;width: 40px;background-color: rgba(140,140,140,.2);}
  .maskLayer-enter-active,.maskLayer-leave{
       transition: all .3s linear;
  }
  .maskLayer-enter{
    opacity: 0;
    transform: translateX(140px);
  }
  .maskLayer-leave-active{
    transition: all .2s linear;
    opacity: 0;
    transform: translateX(-140px);
  }
  .btn_list{text-align: center;}
  .btn_list>button{border: 1px solid #ffb712;border-radius: 5px;width: 120px;height: 24px;color: #ffb712;background-color: #fff;margin-right: 10px;}
  button{border:none;outline: none;}
  .item_content{padding:30px 20px;}
  .item_content h2{font-size: 20px;line-height: 28px;}
  .item_content p{line-height: 16px;font-size: 12px;padding: 10px 0;color: #494949;}
  p.title{color: #aaa;}
  .tag_list li{display: inline-block;font-size: 14px;padding: 5px;border-radius: 20px;background-color: #f5f5f5;margin-right: 5px;}
  .video_cont video{margin: 0 auto;width: 100%;display: block;font-size: 0;}
  .video_side{padding-bottom: 20px;}
  .item_content time{font-size: 12px;}
  .comment_list{padding-top: 10px;}
  .comment_list>li{padding-bottom: 15px;}
  .comment_list>li>div{float: left;}
  .comment_list p{padding: 2px 0;}
  .img_cont{width: 40px;height: 40px;background-color: rgba(140,140,140,.2);border-radius: 50%;margin-right: 15px;font-size: 16px;text-align: center;line-height: 40px;color: #fff;}
  p.duanpin{text-align: center;color: #42bd56;background-color: rgba(140,140,140,.2);font-size: 14px;letter-spacing: 1px;}
  </style>


