<template>
	<div class="group_reco_item clear">
		<div class="group_reco_icon">
			<slot name="groupIcon"></slot>
		</div>
		<div class="group_reco_item_cont">
			<h4>{{ groupRecoTitle }}</h4>
			<slot name="groupRecoTxt"></slot>
		</div>
		<div class="group_reco_item_join">
			<div><span>{{ groupRecoNum }}</span>人</div>
			<div class="join_group" @click="joinGroup">
				<img src="../assets/images/ic_group_check_anonymous.png" v-if="!joinPicState">
        <img src="../assets/images/ic_group_checked_anonymous.png" v-else>
			</div>
		</div>
    <transition name="join-fade">
      <div class="crare_join" v-if="joinState">
        已成功{{ joinPicState ? '关注':'取消' }}小组
      </div>
    </transition>
	</div>
</template>

<script>
	
export default{
	// groupRecoTitle: 推荐板块标题  groupRecoTxt: 推荐板块正文 groupRecoNum: 推荐板块已加入的人数
	props:['groupRecoTitle','groupRecoNum'],
  data () {
    return {
      joinState:false,
      joinPicState:false
    }
  },
	methods:{
		joinGroup () {
      window.clearInterval(this.timer);
     this.joinState = true;
     this.joinPicState = !this.joinPicState;
      let _this = this;
      this.timer = window.setTimeout(function(){
        _this.joinState = false;
      },1000);
    }	
	}
}

</script>

<style>
	.group_reco_item{font-family: 'sans-serif';height: 60px;padding-top: 10px;margin-bottom:10px;overflow: hidden;position: relative;}
	.group_reco_icon{width: 60px;height: 60px;padding-right: 15px;float: left;}
	.group_reco_item img{width: 100%;height: 100%;}
	.group_reco_item_cont{width: 160px;float: left;}
	.group_reco_item_cont h4{font-size: 16px;line-height: 26px;color: #333;}
	.group_reco_item_cont p{font-size: 12px;line-height: 16px;word-break: break-all;color: #aaa;height: 32px;overflow: hidden;}
	.group_reco_item_join{font-size: 12px;color: #ccc;padding-top: 20px;float: right;}
	.group_reco_item_join>div{float: left;}
	.group_reco_item_join div:last-child{width: 30px;height: 30px;padding-left: 5px;}
	.group_reco_item_join div:first-child{display: inline-block;line-height: 30px;}
  .crare_join{position: absolute;background-color: rgba(66,189,86,1);color: #fff;padding: 10px;top:0;left: 60px;border-radius: 5px;}

  .join-fade-enter-active,.join-fade-leave-active{
       transition: all .5s ease-out;
  }
  .join-fade-enter {
    opacity: 0;
    transform: translateY(30px);
  }
  .join-fade-leave-active{
    opacity: 0;
    transform: translateY(-30px);
  }
</style>


