<template>
	<div class="audio_item_cont" v-show="bol">
		<div class="audio_item_cont_top clear">
			<div class="audio_author_icon">
				<slot name="audio_author_icon"></slot>
			</div>
			<div class="audio_author_msg">
				<p>{{ authorName }}</p>
				<p><span>{{ authorNum }}</span>人关注</p>
			</div>
			<div class="audio_author_care_btn" @click="careAuthor">
				{{careTxt ? '已关注' : '关注' }}
			</div>
			<div class="audio_author_close_btn" @click="closeItem()">
				<img src="../assets/images/ic_group_history_delete.png">
			</div>
		</div>
		<div  class="audio_item_cont_mid">
			<slot name="audio_item_txt"></slot>
			<div class="audio_item_txt_pic clear">
				<slot name="audio_item_txt_img"></slot>
			</div>
		</div>
		<div class="audio_item_cont_bot">
			<div @click="liekPicChange" class="clear">
				<div class="pic">
					<img src="../assets/images/ic_like.png" v-if="picShow">
					<img src="../assets/images/ic_liked.png" v-else>
				</div>
				<span>{{ lNum }}</span>
			</div>
			<div @click="showDiscuss" class="clear">
        <div>
          <div class="pic comment_pic">
            <img src="../assets/images/ic_comment_full.png">
          </div>
          <span>{{ disNum }}</span>
        </div>
			</div>
		</div>
    <div v-if="showComment" class="comment_list_wrap">
     <ul class="comment_list">
      <li v-for="item in comment"> 
        <div>{{item.userNmae}}</div>
        <time>{{item.data}}</time>
        <p>{{item.comment}}</p>
      </li>    
      </ul>
    </div>
   
    <transition name="detail-fade">
      <div class="crare_ss" v-if="showCare">
        已成功{{ this.careTxt ? '' : '取消'}}关注{{this.authorName}}
      </div>
    </transition>
		
	</div>
</template>

<script>
export default{
	props:['authorName','authorNum','likeNum','disNum'],
	data:function(){
		return {
			bol:true,
			picShow:true,
			lNum:0,
      author:'',
      showCare:false,
      timer: null,
      showComment: false,
      careTxt: false,
      comment:[
        {
          "data": "2017-05-25 1:16:55",
          "userNmae": "MIS奥卡",
          "comment": "修仙福利!画咱收了!"
        },
        {
          "data": "2017-05-26 10:16:55",
          "userNmae": "aamikoduaa",
          "comment": "快把我从折叠区里放出来！！"
        },
        {
          "data": "2017-05-27 10:16:55",
          "userNmae": "MIS奥卡",
          "comment": "很赞啊！！"
        },
        {
          "data": "2017-05-28 1:16:55",
          "userNmae": "啊阿莫sdkn",
          "comment": "画不错!意境已经超脱凡胎!"
        },
        {
          "data": "2017-05-28 11:10:55",
          "userNmae": "暗杀宝贝儿~",
          "comment": "画不错!意境已经超脱凡胎!"
        }
      ]
		}
	},
	methods:{
		closeItem:function(){
			this.bol = false;
      console.log(this.comment)
		},
		careAuthor:function(){
      window.clearInterval(this.timer);
      this.showCare = !this.showCare;
			this.author = this.authorName;

      this.careTxt = !this.careTxt;

      let _this = this;
      this.timer = window.setTimeout(function(){
        _this.showCare = !_this.showCare;
      },1000);
		},
		liekPicChange:function(){

			this.picShow ? this.lNum++ : this.lNum--;
			this.picShow = !this.picShow;

		},
		showDiscuss:function(){
      this.showComment = !this.showComment;
    }
  }
}
</script>

<style>

	.audio_item_cont{border-radius: 10px;box-shadow: 0 0 5px rgba(0,0,0,.1);background-color: #fff;padding:15px 15px 10px 10px;position: relative;font-family: sans-serif;margin-bottom: 10px;}
	.audio_author_icon{width: 50px;height: 50px;padding-right: 15px;}
	.audio_author_icon img{width: 100%;height: 100%;border-radius: 50%;}
	.audio_item_cont_top>div{float: left;}
	.audio_author_msg{padding-top: 5px;font-size: 13px;color: #b2b2b2;}
	.audio_author_msg p:first-child{font-size: 16px;font-weight: 700;line-height: 28px;color: #6d6d6d;}

	div.audio_author_care_btn{width: 70px;height: 26px;background-color: #42c055;text-align: center;line-height: 26px;color: #fff;font-size: 12px;font-weight: bold;border-radius: 3px;margin-top: 10px;float: right;}
	.audio_author_close_btn{width:12px;height:12px;position: absolute;right: 5px;top: 0;}
	.audio_author_close_btn img{width: 100%;height: 100%;}

	.audio_item_cont_mid{padding:10px 0 0 65px;}
	.audio_item_cont_mid p{font-size: 14px;font-weight: bold;color: #6d6d6d;padding-bottom: 10px;}

	.audio_item_txt_pic,.audio_item_txt_pic img{width: 100%;}
	.audio_item_txt_pic img{float: left;}

	.audio_item_cont_bot{padding:10px 0  0 65px;height: 30px;}
	.audio_item_cont_bot>div{float: left;}
	.audio_item_cont_bot .pic{width: 25px;float: left;padding-right: 5px;}
	.audio_item_cont_bot .comment_pic{width: 20px;    padding-top: 2px;}
	.audio_item_cont_bot>div:last-child{padding-left: 30px;}
	.audio_item_cont_bot img{width: 100%;}
	.audio_item_cont_bot span{float: left;line-height: 26px;font-size: 13px;color: #ccc;}
	.crare_ss{position: absolute;padding:5px;left:20%;top: -5px;background-color: #42c055;color: #fff;text-align: center;font-size: 14px;line-height: 18px;font-weight:600;border-radius: 5px;}

	.detail-fade-enter-active,.detail-fade-leave-active{
	     transition: all .3s ease-out;
  }
  .detail-fade-enter,.detail-fade-leave-active {
    opacity: 0;
    transform: translateY(-15px);
  }
  .comment_list_wrap{padding: 28px 65px 28px ;}
  .comment_list li{padding: 10px;border-bottom: 1px solid rgba(102,102,102,.8);}
  .comment_list div{font-weight: bold;font-size: 16px;}
  .comment_list time{font-size: 12px;padding: 5px 0;display: inline-block;}
  .comment_list p{font-size: 14px;line-height: 16px;}
</style>


