<template>
	<div class="item" @click="turnPage()">
		<span v-show="bol">
			<slot name="normalImg"></slot>
		</span>
		<span v-show="!bol">
			<slot name="activeImg"></slot>
		</span>
		<p :class="{item_active:!bol}">{{ txt }}</p>
	</div>
</template>

<script>
	export default{
		props:['txt','id'],
		data:function(){
			return {
				
			}
		},
		computed:{
			bol:function(){
				if (this.id == this.$parent.sel) {
					return false;
				}
				return true;
			}
		},
		methods:{
			turnPage:function(){
				this.$router.push('/'+this.id);
				this.$emit('sendval',this.id);
			}
		}
	}
</script>

<style>
	.item{width: 20%;height: 100%;float: left;font-size: 14px;}
	.item_active{color: #42bd56;}
</style>


