<template>
	<div class="group_reco_wrap">
		<div class="wrap_title">
			<h3>{{ wrapTitle }}</h3>
		</div>
		<slot></slot>
	</div>
</template>

<script>
	export default {
		// wrapTitle 推荐栏目大标题 
		props:['wrapTitle']
	}
</script>

<style>
	.wrap_title{border-bottom: 1px solid #ccc;position: relative;padding-top: 30px;margin-bottom: 30px;}
	.wrap_title h3{position: absolute;background-color: #fff;left: 50%;margin-left: -30px;width: 60px;text-align: center;top: 50%;font-size: 16px;padding-top: 5px;color: #666}
</style>


